import React from "react";
import { ForceGraph2D, ForceGraph3D } from "react-force-graph";
import { data } from "./data";


const { useRef } = React;




function ForceWords() {
  const fgRef = useRef();
  return (
    <>
    <p> Zoom in on a node to see the words associated with it - rotate the map into place by clicking on the black space.</p>
    <p>(Refresh your window if the chart seems a bit off - it may not work well on mobile devices)</p>
    <ForceGraph3D
      width={window.innerWidth*(0.7)}
      height={window.innerHeight*(0.6)}
      ref={fgRef}
      linkWidth={1}
      // linkDirectionalParticleColor={() => "red"}
      // linkDirectionalParticleWidth={6}
      linkHoverPrecision={10}
      graphData={data}
      nodeId="id"
      nodeLabel="id"
      showNavInfo="true"
      nodeAutoColorBy= "group"
      // nodeColor="black"
      nodeVal= "size"
      d3VelocityDecay = {0.3}
      nodeRelSize ={2}
      nodeResolution = {15}

      // onLinkClick={link => fgRef.current.emitParticle(link)}
    />
    </>
  );
}

const rootElement = document.getElementById("root");
// render(<Forces />, rootElement);

export default ForceWords;
