function Forces() {
  const fgRef = useRef();
  return (
    <>
    <p> Zoom in on a node to see the words associated with it - drag the map into place by clicking on the white space.</p>
    <p>(Refresh your window if the chart seems a bit off)</p>
    <ForceGraph2D
      width={window.innerWidth*(0.7)}
      height={750}
      ref={fgRef}
      linkWidth={3}
      linkAutoColorBy="group"
      // linkDirectionalParticleColor={() => "red"}
      // linkDirectionalParticleWidth={6}
      linkHoverPrecision={10}
      graphData={data}
      nodeId="id"
      nodeLabel="id"
      showNavInfo="true"
      // nodeColor="black"
      nodeRelSize={8}
      nodeCanvasObject={(node, ctx, globalScale) => {
        const label = node.id;
        const fontSize = 12/globalScale;
        ctx.font = `${fontSize}px Sans-Serif`;
        const textWidth = ctx.measureText(label).width;
        const bckgDimensions = [textWidth, fontSize].map(n => n + fontSize * 0.2); // some padding

        // ctx.fillStyle = 'rgba(0, 0, 0, 0.8)';
        // ctx.fillRect(node.x - bckgDimensions[0] / 2, node.y - bckgDimensions[1] / 2, ...bckgDimensions);

        ctx.textAlign = 'center';
        ctx.textBaseline = 'middle';
        ctx.fillStyle = node.color;
        ctx.fillText(label, node.x, node.y);

        node.__bckgDimensions = bckgDimensions; // to re-use in nodePointerAreaPaint
      }}
      nodePointerAreaPaint={(node, color, ctx) => {
        ctx.fillStyle = color;
        const bckgDimensions = node.__bckgDimensions;
        bckgDimensions && ctx.fillRect(node.x - bckgDimensions[0] / 2, node.y - bckgDimensions[1] / 2, ...bckgDimensions);
      }}

      // onLinkClick={link => fgRef.current.emitParticle(link)}
    />
    </>
  );
}

const rootElement = document.getElementById("root");
